(function ($) {

  $.fn.harmonyDatetimepicker = function(opt){

    var $wrap = $(this);
    var $month = $wrap.find('.month');
    var $hour = $wrap.find('.hour');
    var $minute = $wrap.find('.minute');
    var $hourList = $hour.find('.list_opt');
    var $minuteList = $minute.find('.list_opt');
    var $hourText =  $hour.find('.btn_selected');
    var $minuteText =  $minute.find('.btn_selected');

    var options = $.extend({
      autoclose: true,
      format: 'yyyy.mm.dd',
      todayHighlight: true,
      language: 'ko'
    }, opt)

    var list = {
      hour: '<li><a class="link_option" data-value="0">0</a></li>\
      <li><a class="link_option" data-value="1">1</a></li>\
    <li><a class="link_option" data-value="2">2</a></li>\
    <li><a class="link_option" data-value="3">3</a></li>\
    <li><a class="link_option" data-value="4">4</a></li>\
    <li><a class="link_option" data-value="5">5</a></li>\
    <li><a class="link_option" data-value="6">6</a></li>\
    <li><a class="link_option" data-value="7">7</a></li>\
    <li><a class="link_option" data-value="8">8</a></li>\
    <li><a class="link_option" data-value="9">9</a></li>\
    <li><a class="link_option" data-value="10">10</a></li>\
    <li><a class="link_option" data-value="11">11</a></li>\
    <li><a class="link_option" data-value="12">12</a></li>\
    <li><a class="link_option" data-value="13">13</a></li>\
    <li><a class="link_option" data-value="14">14</a></li>\
    <li><a class="link_option" data-value="15">15</a></li>\
    <li><a class="link_option" data-value="16">16</a></li>\
    <li><a class="link_option" data-value="17">17</a></li>\
    <li><a class="link_option" data-value="18">18</a></li>\
    <li><a class="link_option" data-value="19">19</a></li>\
    <li><a class="link_option" data-value="20">20</a></li>\
    <li><a class="link_option" data-value="21">21</a></li>\
    <li><a class="link_option" data-value="22">22</a></li>\
    <li><a class="link_option" data-value="23">23</a></li>',

      minute: '<li><a class="link_option" data-value="0">0</a></li>\
    <li><a class="link_option" data-value="10">10</a></li>\
    <li><a class="link_option" data-value="20">20</a></li>\
    <li><a class="link_option" data-value="30">30</a></li>\
    <li><a class="link_option" data-value="40">40</a></li>\
    <li><a class="link_option" data-value="50">50</a></li>'
    };

    function resetLayer() {
      $hour.removeClass('on');
      $minute.removeClass('on');
    }

    function showHourLayer(event) {
      resetLayer();
      $hour.addClass('on');

      return false;
    }

    function showMinuteLayer(event) {
      resetLayer();
      $minute.addClass('on');

      return false;
    }

    function selectedHour(event) {
      var $target = $(event.target);
      var value = $target.data('value');
      $hourText.text(value + '');
    }

    function selectedMinute(event) {
      var $target = $(event.target);
      var value = $target.data('value');
      $minuteText.text(value + '');
    }

    function addEvent() {

      $month.find('.btn_selected')
        .datepicker(options)
        .on('changeDate', function (event) {
          $(event.currentTarget).html(event.format());
        })
        .on('show', function (event) {

          $hour.removeClass('on');
          $minute.removeClass('on');

        });

      $hourText.on('click', showHourLayer);
      $minuteText.on('click', showMinuteLayer);

      $hourList.on('click', 'a', selectedHour);
      $minuteList.on('click', 'a', selectedMinute);

      $(document.body).click(resetLayer);
    }

    function insertList() {
      $hourList.html(list.hour);
      $minuteList.html(list.minute);
    }

    insertList();
    addEvent();
  }
})(jQuery);
